.product-reviews-container {
	width: 100%;
	height: 100vh;
	box-sizing: border-box;
	background-color: white;
	border-left: 1px solid rgba(0, 0, 0, 0.158);
}

.product-reviews-form {
	width: 20vmax;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: auto;
	padding: 3vmax;
	background-color: white;
}

.product-reviews-form-heading {
	color: rgba(0, 0, 0, 0.733);
	font: 300 2vmax "Roboto";
	text-align: center;
}

.product-reviews-form > div {
	display: flex;
	width: 100%;
	align-items: center;
	margin: 2vmax;
}

.product-reviews-form > div > input {
	padding: 1vmax 4vmax;
	padding-right: 1vmax;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid rgba(0, 0, 0, 0.267);
	border-radius: 4px;
	font: 300 0.9vmax "Roboto";
	outline: none;
}

.product-reviews-form > div > svg {
	position: absolute;
	transform: translateX(1vmax);
	font-size: 1.6vmax;
	color: rgba(0, 0, 0, 0.623);
}

@media screen and (max-width: 600px) {
	.product-reviews-container {
		border-left: none;
		border-top: 1px solid rgba(0, 0, 0, 0.158);
	}
	.product-reviews-form > div > input {
		padding: 2.5vmax 5vmax;
		font: 300 1.7vmax cursive;
		width: 100%;
	}
	.product-reviews-form > div > svg {
		font-size: 2.8vmax;
	}
}
