footer {
    margin-top: 10vmax;
    padding: 2vmax;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    align-items: center;
}

.left-footer {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.left-footer > h4 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1vmax;
}

.left-footer > p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.left-footer > img {
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
}

.mid-footer {
    width: 60%;
    text-align: center;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.mid-footer > h1 {
    font-size: 4vmax;
    font-family: "Roboto";
    color: #eb4034;
}

.mid-footer > p {
    max-width: 60%;
    margin: 1vmax auto;
}

.right-footer {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.right-footer > h4 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1.4vmax;
    text-decoration: underline;
}

.right-footer > a {
    text-decoration: none;
    font-size: 1.3vmax;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
    transition: 0.5s;
    margin: 0.5vmax;
}

.right-footer > a:hover {
    color: #eb4034;
}