.reset-password-container {
	width: 100vw;
	height: 100vh;
	max-width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgb(231, 231, 231);
	position: fixed;
	top: 0%;
	left: 0;
}

.reset-password-box {
	background-color: white;
	width: 25vw;
	height: 40vh;
	box-sizing: border-box;
	overflow: hidden;
}

.reset-password-heading {
	text-align: center;
	border-bottom: 1px solid rgba(0, 0, 0, 0.205);
	width: 50%;
	margin: auto;
	padding: 1.3vmax;
	color: rgba(0, 0, 0, 0.664);
	font: 400 1.3vmax "Roboto";
}

.reset-password-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: auto;
	padding: 2vmax;
	justify-content: space-evenly;
	height: 70%;
	transition: all 0.5s;
}

.reset-password-form > div {
	display: flex;
	width: 100%;
	align-items: center;
}

.reset-password-form > div > input {
	padding: 1vmax 4vmax;
	padding-right: 1vmax;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid rgba(0, 0, 0, 0.267);
	border-radius: 4px;
	outline: none;
	font: 300 0.9vmax cursive;
}

.reset-password-form > div > svg {
	position: absolute;
	transform: translateX(1vmax);
	font-size: 1.6vmax;
	color: rgba(0, 0, 0, 0.623);
}

.reset-password-btn {
	border: none;
	color: white;
	background-color: tomato;
	font: 300 0.9vmax "Roboto";
	width: 100%;
	padding: 0.8vmax;
	cursor: pointer;
	transition: all 0.5s;
	border-radius: 4px;
	outline: none;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.reset-password-btn:hover {
	background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
	.reset-password-container {
		background-color: white;
	}

	.reset-password-box {
		width: 100vw;
		height: 95vh;
	}

	.reset-password-form {
		padding: 5vmax;
	}

	.reset-password-heading {
		font: 400 3vmax "Roboto";
	}

	.reset-password-form > div > input {
		padding: 2.5vmax 5vmax;
		font: 300 1.7vmax cursive;
	}

	.reset-password-form > div > svg {
		font-size: 2.8vmax;
	}

	.reset-password-btn {
		font: 300 1.9vmax "Roboto";
		padding: 1.8vmax;
	}
}
